@use "global/variables" as v;
@use "global/mixins" as m;
@use "sass:color";
$fondo-tarjeta: #1e293b;
$texto: #ffffff;
$texto-secundario: #cbd5e1;
$bg-input: #0f172a;

.contacto__formulario {
  background-color: $fondo-tarjeta;
  padding: 2.5rem;
  border-radius: 1rem;
  margin-top: 1rem;
  border: 1px solid color.scale(v.$color-primario, $lightness: -50%);
  margin-bottom: 10rem;
  h3 {
    font-size: 1.8rem;
    font-weight: 900;
    color: $texto;
    margin-bottom: 2rem;
  }

  .formulario {
    display: flex;
    flex-direction: column;
    gap: 2rem;

    .campo {
      display: flex;
      flex-direction: column;
      gap: 0.5rem;

      label {
        color: $texto;
        font-weight: 600;
        font-size: 1rem;
      }

      input,
      textarea {
        background-color: $bg-input;
        border: none;
        outline: none;
        padding: 1rem;
        border-radius: 0.5rem;
        color: $texto;
        font-size: 1rem;

        &::placeholder {
          color: $texto-secundario;
        }
      }

      textarea {
        resize: vertical;
        min-height: 150px;
      }
    }

    .Btn-3 {
      width: 100%;
      margin-top: 1rem;
    }
  }
}
