@use "global/variables" as v;
@use "global/mixins" as m;
@use "sass:color";
.galeria {
  color: v.$White;
  margin-top: 4rem;
  text-align: center;

  h2 {
    text-transform: uppercase;
    font-weight: 900;
    margin: 0;
    font-size: 3.4rem;
  }

  p {
    color: v.$texto-secundario;
  }

  .galeria__items {
    display: flex;
    flex-direction: column;
    gap: 2rem;
    padding: 1rem;
    @include m.desktop {
      display: grid;
      grid-template-columns: repeat(2, 1fr);
    }
    .item {
      position: relative;
      overflow: hidden;
      border-radius: 1rem;
      cursor: pointer;

      img {
        width: 100%;
        display: block;
        transition: 0.4s ease-in-out;
      }

      /* Overlay oscuro */
      &::after {
        content: "";
        position: absolute;
        inset: 0;
        background: rgba(0, 0, 0, 0);
        transition: 0.4s ease-in-out;
      }

      /* Texto */
      p {
        position: absolute;
        bottom: 1.5rem;
        left: 1.5rem;
        font-size: 1.4rem;
        font-weight: 700;
        opacity: 0;
        transform: translateY(20px);
        transition: 0.4s ease-in-out;
        z-index: 2;
      }

      /* Hover: imagen oscura y texto visible */
      &:hover {
        img {
          transform: scale(1.05);
        }
        &::after {
          background: rgba(0, 0, 0, 0.45);
        }
        p {
          opacity: 1;
          transform: translateY(0);
        }
      }
    }
  }
}
