@use "global/variables" as v;
@use "global/mixins" as m;

.form__size {
  margin-bottom: 2rem;
  select {
    color: v.$hover-focus;
    font-weight: bold;
    border-radius: 5px;
    background-color: v.$fondo-tarjeta;

    option {
      color: v.$White;
    }
  }
  select,
  option {
    text-transform: uppercase;
  }
}
