@use "global/variables" as v;
@use "global/mixins" as m;
@use "sass:color";
.contacto {
  margin-top: 4rem;
  background-color: color.scale(v.$fondo-tarjeta, $lightness: -50%);
  color: v.$texto-secundario;
  padding: 4rem;
  .contacto__grid {
    .contacto__mapa {
      grid-row: 3 / 2;
      padding: 1rem;
    }

    @include m.desktop {
      display: grid;
      grid-template-columns: repeat(2, 1fr);
    }
  }
  h2 {
    margin: 0;
    padding-top: 2rem;
    color: v.$color-primario;
    font-weight: 900;
    font-size: 3.6rem;
    text-transform: uppercase;
  }
  .contacto__top {
    text-align: center;
    p {
      margin: 0;
      font-size: 1.8rem;
      font-weight: 300;
    }
  }

  .contacto__informacion {
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 2rem;
    padding: 1rem;
    .contacto__informacion--item {
      background-color: v.$fondo-tarjeta;
      border: 1px solid color.scale(v.$color-primario, $lightness: -50%);
      padding: 1rem;
      border-radius: 10px;
      width: -webkit-fill-available;
      .item__top {
        display: flex;
        align-items: center;
        gap: 0.4rem;
        .resaltar {
          font-size: 1.8rem;
        }
      }
      .item__bottom {
        p {
        }
      }
    }
  }
}
