@use "global/variables" as v;
@use "global/mixins" as m;
@use "sass:color";
.footer {
  color: v.$texto-secundario;
  margin-top: 4rem;
}

.footer__container {
  padding: 1rem;
  @include m.desktop {
    display: grid;
    grid-template-columns: repeat(4, 1fr);
    gap: 3rem;
    align-items: baseline;
  }

  .footer__col {
    h2 {
      margin: 0;
    }
  }
}

.footer__bottom {
  margin-top: 3rem;
  text-align: center;
  padding-top: 2rem;
  border-top: 1px solid rgba(255, 255, 255, 0.1);
}

.footer__links {
  h3 {
    padding: 0;
    margin: 0;
  }
  ul {
    list-style: none;
    display: flex;
    flex-direction: column;
    padding: 0;
    gap: 1rem;
    li {
      a {
        color: v.$texto-secundario;
      }
    }
  }
}

.social__icons {
  display: flex;
  flex-direction: row;
  align-items: center;
  gap: 2rem;

  .social__icon {
    background-color: color.scale(v.$prueba, $lightness: -55%);
    opacity: 0.7;
    padding: 1rem;
    border-radius: 9999px;
    width: 4rem;
    height: 4rem;
    display: flex;
    align-items: center;
    svg {
      color: v.$color-primario;
    }
    &:hover {
      background-color: color.scale(v.$prueba, $lightness: 0%);
      svg {
        color: v.$color-secundario;
        opacity: 1;
      }
    }
  }
}

.footer__bottom {
  padding: 3rem;
}
