@use "global/variables" as v;
@use "global/mixins" as m;
.alerta__agregado {
  position: fixed;
  top: 1.5rem;
  right: 1.5rem;
  z-index: 9999;

  display: flex;
  align-items: center;
  gap: 0.75rem;

  background-color: #0f151e;
  color: #fff;
  padding: 1rem 1.25rem;
  border-radius: 14px;
  box-shadow: 0 10px 30px rgba(0, 0, 0, 0.35);

  font-size: 0.95rem;
  line-height: 1.3;

  opacity: 0;
  transform: translateY(-20px) scale(0.95);
  animation: alertaEntrada 0.4s ease-out forwards,
    alertaSalida 0.4s ease-in forwards 3s;
}

/* Texto */
.alerta__agregado p {
  margin: 0;
}

/* Palomita */
.alerta__agregado::before {
  content: "‚úî";
  display: flex;
  align-items: center;
  justify-content: center;

  width: 36px;
  height: 36px;
  border-radius: 50%;

  background-color: #fbbf24;
  color: #0f151e;
  font-weight: bold;
  font-size: 1.1rem;

  animation: checkPop 0.4s ease-out;
}

/* Animaciones */
@keyframes alertaEntrada {
  to {
    opacity: 1;
    transform: translateY(0) scale(1);
  }
}

@keyframes alertaSalida {
  to {
    opacity: 0;
    transform: translateY(-20px) scale(0.95);
  }
}

@keyframes checkPop {
  0% {
    transform: scale(0.3);
    opacity: 0;
  }
  80% {
    transform: scale(1.15);
  }
  100% {
    transform: scale(1);
    opacity: 1;
  }
}

.alerta__eliminado {
  position: fixed;
  top: 1.5rem;
  right: 1.5rem;
  z-index: 9999;

  display: flex;
  align-items: center;
  gap: 0.75rem;

  background-color: #0f151e;
  color: #fff;
  padding: 1rem 1.25rem;
  border-radius: 14px;
  box-shadow: 0 10px 30px rgba(0, 0, 0, 0.35);

  font-size: 0.95rem;
  line-height: 1.3;

  opacity: 0;
  transform: translateY(-20px) scale(0.95);
  animation: alertaEntrada 0.4s ease-out forwards,
    alertaSalida 0.4s ease-in forwards 3s;
}

/* Texto */
.alerta__eliminado p {
  margin: 0;
}

/* Icono eliminado */
.alerta__eliminado::before {
  content: "üóë";
  display: flex;
  align-items: center;
  justify-content: center;

  width: 36px;
  height: 36px;
  border-radius: 50%;

  background-color: #ef4444; /* rojo moderno */
  color: #fff;
  font-size: 1.1rem;

  animation: deletePop 0.4s ease-out;
}

/* Animaci√≥n icono */
@keyframes deletePop {
  0% {
    transform: scale(0.3) rotate(-20deg);
    opacity: 0;
  }
  80% {
    transform: scale(1.15) rotate(5deg);
  }
  100% {
    transform: scale(1) rotate(0);
    opacity: 1;
  }
}

.alerta__size {
  position: fixed;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%) scale(0.9);
  z-index: 9999;

  display: flex;
  align-items: center;
  gap: 0.9rem;

  background-color: #fbbf24;
  color: #0f151e;
  padding: 1.2rem 1.4rem;

  border-radius: 18px;
  border: 3px solid #0f151e;

  font-size: 1rem;
  font-weight: 600;
  text-align: left;

  box-shadow: 0 15px 35px rgba(0, 0, 0, 0.35),
    0 0 0 6px rgba(251, 191, 36, 0.35);

  opacity: 0;
  animation: alertaSizeEntrada 0.35s ease-out forwards,
    alertaSizePulse 1.4s ease-in-out infinite 0.4s;
}

/* Texto */
.alerta__size p {
  margin: 0;
}

/* Icono warning */
.alerta__size::before {
  content: "‚ö†Ô∏è";
  display: flex;
  align-items: center;
  justify-content: center;

  width: 42px;
  height: 42px;
  border-radius: 50%;

  background-color: #0f151e;
  color: #fbbf24;
  font-size: 1.3rem;

  animation: warningShake 0.5s ease-out;
}

/* Animaciones */
@keyframes alertaSizeEntrada {
  to {
    opacity: 1;
    transform: translate(-50%, -50%) scale(1);
  }
}

@keyframes alertaSizePulse {
  0% {
    box-shadow: 0 15px 35px rgba(0, 0, 0, 0.35),
      0 0 0 6px rgba(251, 191, 36, 0.35);
  }
  70% {
    box-shadow: 0 15px 35px rgba(0, 0, 0, 0.35),
      0 0 0 14px rgba(251, 191, 36, 0);
  }
  100% {
    box-shadow: 0 15px 35px rgba(0, 0, 0, 0.35),
      0 0 0 6px rgba(251, 191, 36, 0.35);
  }
}

@keyframes warningShake {
  0% {
    transform: translateX(0);
  }
  25% {
    transform: translateX(-3px);
  }
  50% {
    transform: translateX(3px);
  }
  75% {
    transform: translateX(-2px);
  }
  100% {
    transform: translateX(0);
  }
}
