@use "global/variables" as v;
@use "global/mixins" as m;
@use "sass:color";

.menu__interfaz {
  display: flex;
  flex-direction: column;
  gap: 2rem;
  @include m.desktop {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    gap: 1rem;
  }
  .menu__interfaz--item {
    background-color: color.scale(v.$fondo-tarjeta, $lightness: -20%);
    border-radius: 12px;
    padding: 1rem;

    position: relative; // necesario para que la barra se posicione bien
    overflow: hidden; // opcional si quieres que nada se salga del borde
    transition: transform 0.2s ease;

    //  EFECTO HOVER (barrita inferior)
    &::after {
      content: "";
      position: absolute;
      bottom: 0;
      left: 50%;
      transform: translateX(-50%);
      width: 0%;
      height: 0.35rem; // grosor de la barrita
      background-color: v.$color-primario;
      border-radius: 50px;
      transition: width 0.35s ease;
      box-shadow: 0 0 20px color.scale(v.$color-primario, $lightness: 20%);
    }

    &:hover::after {
      width: 80%; // cu√°nto se extiende la barra
    }
    .item__texto {
      color: v.$White;
      margin-top: 1rem;
      padding-top: 2rem;
      padding-left: 1rem;
      padding-right: 1rem;
      h3 {
        padding: 0;
        margin: 0;
        font-size: 2.4rem;
      }
      p {
        color: v.$texto-secundario;
        min-height: 5rem;
      }
    }
    .item__precio {
      p {
        font-weight: 900;
        font-size: 2.4rem;
      }

      .item__precio--data {
        color: v.$color-primario;
        display: flex;
        gap: 1rem;
        p {
          font-size: 1.5rem;
          span {
            text-transform: uppercase;
          }
        }
      }
      .item__precio--button {
        display: flex;
        align-items: center;
        justify-content: center; // centra icono + texto
        gap: 0.5rem; // espacio entre icono y texto
        background-color: v.$color-primario;
        border-radius: 12px;
        height: 50%;
        padding: 0.7rem 1.6rem; // \hace el bot√≥n m√°s estirado
        cursor: pointer;

        button {
          background: none;
          border: none;
          font-size: 1.2rem;
          font-weight: bold;
          color: black; // como en el ejemplo
          cursor: pointer;
        }

        img {
          width: 1.3rem; // tama√±o similar al de tu imagen
          filter: brightness(0); // para que sea negro
        }
      }
    }
  }
}
