@use "global/variables" as v;
@use "global/mixins" as m;
@use "sass:color";
.cart__panel {
  background-color: color.scale(v.$fondo-tarjeta, $lightness: -50%);
  padding: 2rem;
  margin-top: 5rem;

  h2 {
    text-align: center;
    color: v.$White;
  }
  .cart__items {
    margin-top: 2rem;
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    @include m.desktop {
      grid-template-columns: repeat(4, 1fr);
    }
    gap: 1rem;
    .cart__item {
      border: 1px solid v.$color-primario;
      border-radius: 5px;
      padding: 1rem;
      color: v.$White;
      &:hover {
        .cart__item--name {
          color: v.$color-primario;
        }
      }
      .cart__item--name {
        text-transform: uppercase;
        font-weight: bold;
        border-bottom: 1px solid v.$color-primario;
      }
    }
    .cart__qty {
      width: 70px;
      height: 40px;

      background-color: #0f151e;
      color: #fbbf24;

      border: 2px solid #fbbf24;
      border-radius: 8px;

      text-align: center;
      font-size: 1rem;
      font-weight: 600;

      outline: none;
      transition: all 0.2s ease;
    }

    /* Hover */
    .cart__qty:hover {
      background-color: #16202c;
    }

    /* Focus */
    .cart__qty:focus {
      box-shadow: 0 0 0 2px rgba(251, 191, 36, 0.4);
    }

    /* Quitar flechas por defecto (Chrome, Edge, Safari) */
    .cart__qty::-webkit-inner-spin-button,
    .cart__qty::-webkit-outer-spin-button {
      -webkit-appearance: none;
      margin: 0;
    }

    /* Quitar flechas en Firefox */
    .cart__qty {
      -moz-appearance: textfield;
    }

    .cart__remove {
      background-color: transparent;
      color: #fbbf24;

      border: 2px solid #fbbf24;
      border-radius: 8px;

      padding: 6px 12px;

      font-size: 0.9rem;
      font-weight: 600;
      cursor: pointer;

      transition: all 0.2s ease;
    }

    /* Hover */
    .cart__remove:hover {
      background-color: #fbbf24;
      color: #0f151e;
    }

    /* Active (click) */
    .cart__remove:active {
      transform: scale(0.95);
    }

    /* Focus (accesibilidad) */
    .cart__remove:focus {
      outline: none;
      box-shadow: 0 0 0 2px rgba(251, 191, 36, 0.4);
    }
    .cart__total {
      color: v.$White;
    }
  }
}

.empty__car {
  text-align: center;
  color: v.$White;
}

.btn__whatsapp {
  margin-top: 1rem;
  font-size: 1.1rem;
  padding: 1rem;
}

.cart__notes {
  width: 100%;
  min-height: 80px;

  background-color: #0f151e;
  color: #fbbf24;

  border: 2px solid #fbbf24;
  border-radius: 10px;

  padding: 10px 12px;

  font-size: 0.95rem;
  font-weight: 500;
  resize: vertical;

  outline: none;
  transition: all 0.2s ease;
}

/* Placeholder */
.cart__notes::placeholder {
  color: rgba(251, 191, 36, 0.6);
}

/* Hover */
.cart__notes:hover {
  background-color: #16202c;
}

/* Focus */
.cart__notes:focus {
  box-shadow: 0 0 0 2px rgba(251, 191, 36, 0.4);
}

.cart__panel--bottom {
  margin-top: 2rem;
}
