@use "global/variables" as v;
@use "sass:color";
@mixin telefono {
  @media (min-width: v.$telefono) {
    @content;
  }
}

@mixin tablet {
  @media (min-width: v.$tablet) {
    @content;
  }
}

@mixin desktop {
  @media (min-width: v.$desktop) {
    @content;
  }
}

@mixin desktopXL {
  @media (min-width: v.$desktopXL) {
    @content;
  }
}

@mixin grid($colums, $gap) {
  display: grid;
  grid-template-columns: repeat($colums, 1fr);
  gap: $gap;
}

@mixin boton($bgBoton, $Display) {
  background-color: $bgBoton;
  text-align: center;
  font-weight: bold;
  padding: 1rem;

  display: $Display;
  color: #000;
  border-radius: 10px;
  border: none;
  font-size: 1.7rem;
  &:hover {
    background-color: color.scale($bgBoton, $lightness: -25%);
    cursor: pointer;
  }
}
@mixin boton-elegante(
  $bgBoton,
  $display: inline-block,
  $colorTexto: v.$color-secundario
) {
  background: linear-gradient(
    to bottom right,
    color.scale($bgBoton, $lightness: 5%),
    // aclara un poco
    color.scale($bgBoton, $lightness: -5%) // oscurece un poco
  );
  color: $colorTexto;
  display: $display;
  font-weight: 600;
  height: 100%;
  font-size: 1.6rem;
  text-align: center;
  letter-spacing: 0.5px;
  padding: 1.2rem 3.2rem;
  border: none;
  border-radius: 0.8rem;
  box-shadow: 0 4px 15px rgba(0, 0, 0, 0.15);
  transition: all 0.3s ease;
  text-transform: uppercase;
  position: relative;
  overflow: hidden;

  &:hover {
    background: linear-gradient(
      to bottom right,
      color.scale($bgBoton, $lightness: -3%),
      color.scale($bgBoton, $lightness: -10%)
    );
    transform: translateY(-2px);
    box-shadow: 0 6px 20px rgba(0, 0, 0, 0.25);
    cursor: pointer;
  }

  &::after {
    content: "";
    position: absolute;
    top: 0;
    left: -100%;
    width: 100%;
    height: 100%;
    background: rgba(255, 255, 255, 0.15);
    transform: skewX(-20deg);
    transition: left 0.6s ease;
  }

  &:hover::after {
    left: 120%;
  }
}
// === Mixin para bot√≥n estilo "cheddar" ===
@mixin boton-amarillo($color-borde: #fbbf24, $color-texto-hover: #0f172a) {
  display: inline-block;
  padding: 1.75rem 1.5rem;
  border: 2px solid $color-borde;
  font-size: 1.5rem;
  border-radius: 15px; // redondeado tipo pill
  background-color: transparent;
  color: $color-borde;
  font-weight: 900;
  text-align: center;
  cursor: pointer;
  transition: all 0.3s ease-in-out;

  &:hover {
    background-color: $color-borde;
    color: $color-texto-hover;
  }
}
