@use "global/variables" as v;
@use "global/mixins" as m;

.especialidades {
  background: linear-gradient(rgba(29, 40, 58, 0.2), rgba(29, 40, 58, 0.2)),
    #0f172a;

  color: v.$White;

  .especialidades__info {
    text-align: center;
    padding-top: 5rem;
    h2 {
      margin: 0;
      text-transform: uppercase;
      font-weight: 900;
      @include m.desktop {
        font-size: 4.8rem;
      }
    }
    p {
      color: v.$texto-secundario;
      font-size: 1.8rem;
      font-weight: 300;
    }
  }

  .especialidades__contenedor {
    display: flex;
    flex-direction: column;
    gap: 2rem;
    padding: 2rem;
    @include m.desktop {
      display: grid;
      grid-template-columns: repeat(3, 1fr);
      gap: 4rem;
    }
    .especialidades__contenedor--tarjeta {
      border: 1px solid v.$color-primario;
      border-radius: 10px;
      padding: 3rem;

      &:hover {
        opacity: 0.7;
        .tarjeta__informacion {
          h3 {
            color: v.$color-primario;
          }
        }
      }
      .tarjeta__icon {
        display: flex;
        align-items: center;
        gap: 1rem;
        background-color: v.$color-primario;
        width: 11rem;
        justify-content: center;
        border-radius: 10px;
        font-size: 1.2rem;
        color: v.$color-secundario;
      }
      .tarjeta__informacion {
        h3 {
          font-weight: 900;
          font-size: 2.4rem;
        }
        p {
          height: 12.75rem;
          font-size: 1.8rem;
        }
      }
      .tarjeta__precio {
        display: flex;
        justify-content: space-between;
        padding: 2rem;
        p {
          font-size: 3.4rem;
          color: v.$color-primario;
          font-weight: 900;
          margin: 0;
        }
      }
    }
  }
}
