@use "global/variables" as v;
@use "global/mixins" as m;
@use "sass:color";
.paquetes {
  background-color: color.scale(v.$fondo-tarjeta, $lightness: -50%);
  padding-top: 7rem;
  padding-bottom: 4rem;
  .paquetes__texto {
    text-align: center;
    h2 {
      color: v.$White;
      text-transform: uppercase;
      font-weight: 900;
      margin: 0;
      font-size: 3.6rem;
    }
    p {
      color: v.$texto-secundario;
      font-size: 1.8rem;
    }
  }
  .paquetes__contenedor {
    display: flex;
    flex-direction: column;
    padding: 1rem;
    gap: 2rem;
    @include m.desktop {
      display: grid;
      grid-template-columns: repeat(3, 1fr);
      gap: 4rem;
    }
    .paquetes__contenedor--item {
      background-color: color.scale(v.$fondo-tarjeta, $lightness: -20%);
      padding: 2rem;
      border-radius: 10px;
      border: 1px solid color.scale(v.$prueba, $lightness: -50%);
      display: flex;
      flex-direction: column;

      &:hover {
        border: 1px solid v.$prueba;
        .item__top {
          svg {
            width: 3.4rem;
          }
        }
      }
      .item__top {
        display: flex;
        align-items: center;
        justify-content: space-between;
        svg {
          width: 3.2rem;
        }
        p {
          background-color: v.$orange;
          font-size: 1.2rem;
          padding: 0.5rem;
          font-weight: bold;
          border-radius: 10px;
        }
      }
      .item__info {
        color: v.$texto-secundario;
        h3 {
          margin: 0;
        }
        ul {
          list-style: none;
          display: flex;
          flex-direction: column;
          gap: 1rem;
          li {
            span {
              color: v.$prueba;
            }
          }
        }
      }
      .item__bottom {
        display: flex;
        align-items: center;
        justify-content: space-between;
        p {
          font-size: 3.6rem;
          font-weight: 900;
        }
      }
    }
  }
}
