@use "global/variables" as v;
@use "global/mixins" as m;
@use "sass:color";

.menu {
  background-color: color.scale(v.$fondo-tarjeta, $lightness: -50%);
  padding: 2rem;
  .menu__top {
    color: v.$White;
    display: flex;
    flex-direction: column;
    align-items: center;
    padding: 1rem;
    h2 {
      text-transform: uppercase;
      font-weight: 900;
      margin: 0;
      padding: 0;
      @include m.desktop {
        font-size: 3.4rem;
      }
    }

    p {
      font-weight: 300;
      font-size: 1.8rem;
      text-align: justify;
    }
  }
  .menu__buttons,
  .menu__buttons button {
    background-color: color.scale(v.$fondo-tarjeta, $lightness: -20%);
  }
  .menu__buttons {
    padding: 2rem;
    display: grid;
    grid-template-columns: repeat(4, 1fr);
    border-radius: 1rem;
    margin-bottom: 1rem;
    @include m.desktop {
      display: flex;
      justify-content: space-around;
      padding: 0.5rem;
    }
    button {
      border: none;
      padding: 1rem;
      color: v.$White;
      font-weight: bold;
      .menu__buttons--nombre {
        font-size: 0;
        @include m.desktop {
          font-size: 12px;
        }
      }

      &.menu__buttons--active {
        background-color: v.$color-primario;
        border-radius: 10px;
        color: v.$color-secundario;
      }
      &:hover {
        cursor: pointer;
      }
    }
  }
}
